.header {
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05);

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    max-width: 1200px;
    padding: 0 50px;
    margin: 0 auto;
  }

  &__image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all $transition;
  }

  &__logo {
    position: relative;
    width: 40px;
    height: 40px;

    @media (any-hover: hover) {
      &:hover {
        .header__image {
          transform: scale(1.1);
        }
      }
    }
  }

  &__menu {
    @media (max-width: $mobile-L-width) {
      display: none;
    }
  }
}
